import * as React from "react";
import styled, { useTheme } from "styled-components";
import whiteImage from "@images/wip_white.png";
import darkImage from "@images/wip_black.png";

const Layout = styled.div.withConfig({ componentId: "WorkInProcess_Layout" })`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: ${({ theme }) => theme.color};
  font-weight: 600;
  height: 100%;

  p {
    margin-top: 4px;
  }
`;

export default function WorkInProcess() {
  const theme = useTheme();
  const currentTheme = theme?.type || "dark";
  const isDarkTheme = currentTheme === "dark";
  return (
    <Layout>
      <img
        src={isDarkTheme ? whiteImage : darkImage}
        alt="still working on it"
      />
      <p>Work In Process</p>
    </Layout>
  );
}
